@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
    --primary-dark: #5651e5;
    --primary-ligth: #709dff;
}
.icon {
    color:var(--primary-dark);
}


@layer base{
    body {
        @apply font-[Popping] 
    }
    li{
        @apply p-4 text-xl
    }
    h1{
        @apply text-3xl md:text-4xl font-bold
    }
    h2{
        @apply text-3xl font-bold
    }
    h3{
        @apply text-xl font-bold
    }
    button{
        @apply p-3 border bg-gradient-to-r from-[var(--primary-dark)] to-[var(--primary-ligth)] text-white rounded-md
    }
    .icon{
        @apply text-2xl cursor-pointer
    }
}